:root {
  /* 
      FIRST WE'LL DEFINE OUR SWATCHES, i.e. THE COMPLEMENTARY
      COLORS THAT WE'LL USE TOGETHER IN MULTIPLE PLACES THAT
      TOGETHER WILL MAKE UP A GIVEN THEME
  */
  --swatch-foundation:    #EEEEDD; /* USED FOR BODY BACKGROUND */
  --swatch-primary:       #E6E6E6; /* DOMINANT COLOR, USED FOR MOST BACKGROUNDS */
  --swatch-complement:    #E1E4CB; /* USED IN FORGROUND OF PRIMARY FOR TEXT AND BORDERS */
  --swatch_contrast:      #111111; /* USED IN MOUSE-OVER SELECTION OF LARGE ELEMENTS */
  --swatch-accent:        #669966; /* USED IN MOUSE-OVER SELECTION OF TEXT AND BORDERS */    
  --my-font-family:          "Robaaaoto";
  --bounceEasing: cubic-bezier(0.51, 0.92, 0.24, 1.15);
}

body {
  /* background-color: var(--swatch-foundation); */
  background-color: white;
  /* font-family: 'Lexend Exa'; */
  font-family: sans-serif;
  color: #ed4e85;
}

#home_banner_container {
  background-image: linear-gradient(to bottom,         
  #b8b808, #636723);
  color: white;
  font-size: 48pt;
  border-color: black;
  border-width: 2px;
  border-style: solid;
  border-radius: 10px;
  padding-left: 10%;
  padding-top: 5%;
  padding-bottom: 5%;
}

.banner-container {
  background-image: linear-gradient(to bottom,         
  #F06E9C, #f0a1be);
  color: white;
  font-size: 20pt;
  padding-left: 5%;
  padding-top: 1%;
  padding-bottom: 1%;
}
.nav-btn, .nav-btn:active, .nav-btn:focus{
  width: 90%;
  margin: 3%;
  font-size: 23px;
  font-weight: 600;
  background-color: #F06E9C !important;
  border: none !important;
  transition: 0.2s;
}
.nav-btn:focus{
  box-shadow: 0 0 0 0.2rem rgb(237 78 144);
}
.nav-btn:hover{
  background-color: #ed4e85 !important;
}

.primary-bg{
  background-color: #F6D8FC;
}

.secondary-bg{
  background-color: #fdf2ff;
}

.white-bg{
  background-color: white !important;
}

.volume-slider{
  width: 30% !important;
  vertical-align: middle;
  color: #ed4e85 !important;
  margin: 15px;
}
.audio-slider{
  width: 85% !important;
  vertical-align: middle;
  color: #ed4e85 !important;
}

.content-center{
  text-align: center;
}

.music-in-list-info{
  text-align: center;
  background-color:#f5e6eb;
  color: #ff0059;
}

.music-list-header{
  text-align: center;
  background-color:#ff9bbca4;
  color: #ffffffdc;
}

.content-left{
  text-align: left;
}

.play-button {
  border: 0;
  background: transparent;
  box-sizing: border-box;
  width: 0;
  height: 74px;
  transition: 100ms all ease;
  cursor: pointer;
  border-style: solid;
  border-width: 37px 0 37px 60px;
  border-color:transparent transparent transparent #ed4e85;
}
.play-button:hover {
  border-color: transparent transparent transparent #F06E9C;
}
.pause-button {
  border: 0;
  background: transparent;
  box-sizing: border-box;
  width: 0;
  height: 74px;
  transition: 100ms all ease;
  cursor: pointer;
  border-color:transparent transparent transparent #ed4e85;
  border-style: double;
  border-width: 0px 0px 0px 60px;
}
.pause-button:hover {
  border-color: transparent transparent transparent #F06E9C;
}

.m-5{
  margin: 5px;
}

.friend-dialog{
  border-radius: 5px;
  border: 1px solid #ed4e85;
  padding: 10px;
  margin: 5px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  position: relative;
  overflow-wrap: break-word;
}

.self-dialog{
  border-radius: 5px;
  border: 1px solid #ed4e85;
  padding: 10px;
  margin: 5px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  position: relative;
  margin-left:auto;
  margin-right:0;
  overflow-wrap: break-word;
}

.dialog-area{
  width: 100%;
  border-top: 1px solid #ed4e85;
  padding: 8px;
  overflow-y: scroll;
  overflow-x: hidden;
  height: 50vh;
}

.input-message{
  position: absolute !important;
  bottom: 0;
  display:table;
  width: 100%;
  border-top: 1px solid #ed4e85;
  padding: 8px;
}

.album-card{
  margin: 10px;
  text-align: center;
}

.feature-album{
  display: flex;
  align-items: center; /* vertical */
  justify-content: center; /* horizontal */
  overflow: hidden;
  height: 250px;
}

.playlist-card{
  border: 2px solid #ed4e85;
  border-radius: 5px;
  padding: 7px;
  color: #ed4e85;
  margin: 10px;
  cursor: pointer;
  width: 20vw;
  transition: 0.3s;
}

.playlist-card:hover{
  color: white;
  background-color: #ed4e85;
}

.border-bottom-accent{
  border-bottom: 1px solid #F6D8FC;
}

.border-bottom-accent:hover{
  background-color: #F6D8FC;
  transition: 0.2s;
}

.search-btn, .search-btn:active, .search-btn:focus{
  /* width: 10%; */
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 5px;
  background-color: #F06E9C !important;
  border: solid 2px #F06E9C !important;
  /* transition: 0.2s; */
}
.search-btn:focus{
  box-shadow: 0 0 0 0.2rem rgb(237 78 144);
}
.search-btn:hover{
  background-color: #ed4e85 !important;
}

.cancel-btn, .cancel-btn:active, .cancel-btn:focus{
  /* width: 10%; */
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 5px;
  background-color: white !important;
  border: solid 2px #F06E9C !important;
  /* transition: 0.2s; */
  color: #ed4e85 !important;
}
.cancel-btn:focus{
  box-shadow: 0 0 0 0.2rem rgb(237 78 144);
}
.cancel-btn:hover{
  background-color: white !important;
}

.modal{
  position: fixed;
  z-index: 1; /* Sit on top */

}

.remove-friend-btn{
  background-color: #ec2727 !important;
  margin-left: 5%;
  border: none !important;
}

.add-friend-btn {
  background-color: #ed4e85 !important;
  margin-left: 5%;
  border: none !important;
}

.pending-friend-btn{
  background-color: gray !important;
  margin-left: 5%;
  border: none !important;
}


.disabled{
  background-color: gray !important;
  border: none !important;
}

.disabled:hover, .disabled:active, .disabled:focus{
  background-color: gray !important;
  cursor: default !important;
  border: none !important;
  box-shadow: none;
}

.reorder-mode-title{
  cursor: pointer;
  font-size: 24px;
  transition: 0.2s;
}

.non-reorder-mode-title{
  transition: 0.2s;
}

a, a:hover {
  color: #ed4e85 !important;
  text-decoration: none !important;
}

.grid-container {
  display: grid;
  grid-template-columns: auto auto auto auto;  /* 4 columns*/
  /* background-color: #2196F3; */
  padding: 10px;
}

.grid-item {
  /* background-color: rgba(255, 255, 255, 0.8); */
  /* border: 1px solid rgba(0, 0, 0, 0.8); */
  padding: 10px;
  font-size: 30px;
  text-align: center;
}

.accordion {
  background-color: #FDF2FF;
  color: #F07DA6;
  cursor: pointer;
  padding: 18px;
  width: 100%;
  text-align: left;
  border: none;
  outline: none;
  transition: 0.4s;
}

.active, .accordion:hover {
  background-color: #f0d4f5;
}

.panel {
  padding: 0 18px;
  background-color: white;
  display: none;
  overflow: hidden;
}

#scroll-to-audio-right {
  position: fixed;
  z-index: 10;
  top: 85%;
  left: 93%;
}

#scroll-to-audio-left {
  position: fixed;
  z-index: 10;
  top: 85%;
  left: 2%;
}

.playlist-image{
  margin: 15px;
  border-radius: 5px;
}
